.checkout-page {
  width: 55%;
  min-height: 90vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 50px auto 0;

  @media screen and (max-width: 800px) {
    width: 80vw;
  }

  .checkout-header {
    width: 100%;
    padding: 10px 0;
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid darkgrey;

    .header-block {
      text-transform: capitalize;
      width: 23%;

      &:last-child {
        width: 8%;
      }
    }
    @media screen and (max-width: 800px) {
      font-size: 10px;
    }
  }

  .total {
    margin-top: 30px;
    margin-left: auto;
    font-size: 36px;

    @media screen and (max-width: 800px) {
      font-size: 20px;
      font-weight: bold;
    }
  }

  .payment-section {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    margin-top: 30px;
    margin-bottom: 20px;

    .test-warning {
      text-align: center;
      font-size: 20px;
      color: red;
      @media screen and (max-width: 800px) {
        font-size: 15px;
      }
    }

    @media screen and (max-width: 800px) {
      flex-direction: column;
    }
  }
}
